import React from "react";
import { Modal, Image } from "react-bootstrap";
import { ENV, IS_TAGEMBED, WASABI_PATH } from "../../actions/api";

const TagembedRedirect = ({ show, onHide }) => {
    const pageContent = window?.APP_CONTENT?.["signup"] ?? null;
    const commonContent = window?.APP_CONTENT?.["common"] ?? null;

    return <Modal size="sm" show={show} className="px-3" onHide={onHide} backdrop="static" aria-labelledby="contained-modal-title-vcenter" keyboard={false} centered>
        <Modal.Body className="p-0">
            <div className="popup-wrap">
                <div className="popup-inn">
                    <div className="popup-head">
                        <Image className="py-2" src={`${WASABI_PATH}app/assets/popup/box-embed.png`} alt="tag" />
                    </div>
                    <p>{IS_TAGEMBED ? pageContent?.existing_t_emb_mbr ?? "" : pageContent?.existing_t_mbr ?? ""}</p>
                    <div className="d-flex align-items-center justify-content-between p-5">
                        <span className="popup-try" onClick={onHide}>{commonContent?.try_another_email ?? ""}</span>
                        <button className="btn btn-primary" onClick={() => window.open(`https://${ENV}.${IS_TAGEMBED ? 'taggbox' : 'tagembed'}.com/accounts/login`, "_self")}>{pageContent?.go_to_login_page ?? ""}</button>
                    </div>
                </div>
            </div>
        </Modal.Body>
    </Modal>
}

export default TagembedRedirect;